<template>
    <div>
        <div ref="map"></div>
    </div>
</template>

<script>
    import { Scene } from '@antv/l7'
    import { ProvinceLayer} from '@antv/l7-district'
    import { GaodeMap } from '@antv/l7-maps'


    export default {
        name: "CQMap",
        data() {
            return {
                scene: null,
                el: null,
                layer: null,
                ProvinceData: []
            }
        },
        created() {
            this.$nextTick(() => {
                this.el = this.$refs.map
                this.scene = new Scene({
                    id: this.el,
                    map: new GaodeMap({
                        center: [116.2825, 39.9],
                        pitch: 0,
                        style: 'light',
                        zoom: 3,
                        minZoom: 3,
                        maxZoom: 10
                    })
                });

                this.scene.on('loaded', () => {


                    // axios.get ... then((data)=>{   })
                    fetch('http://127.0.0.1:1024/cq').then(res=>res.json())
                    .then(data=>{
                        console.log(data.data.properties.cityArray)
                        this.layer = new ProvinceLayer(this.scene, {
                            data:data.data.properties.cityArray ,
                            joinBy: ['NAME_CHN', 'childStatistic'],
                            // joinBy:['NAME_CHN','childS'],
                            adcode:['500000'],
                            depth: 3,
                            label: {
                                field: 'NAME_CHN',
                                textAllowOverlap: true
                            },
                            fill: {
                                color: 'rgba(201,203,204,0.36)'
                            },
                            bubble: {
                                enable: true,
                                size: {
                                    field: 'totalConfirmed',
                                    values: [10, 30]
                                },
                                color: {
                                    field: 'totalConfirmed',  //颜色关联的字段,data中的value
                                    values: (totalConfirmed) => {     //颜色的回调函数
                                        ['#ffde5d', '#ff6e40', '#ff0003', '#BD0804', '#6A0400']


                                        return totalConfirmed < 10 ? '#ffde5d' :
                                            totalConfirmed < 50 ? '#ff6e40' :
                                                totalConfirmed < 100 ? '#ff0003' :
                                                    totalConfirmed < 200 ? '#cd0904' : '#6A0400'

                                    }
                                },
                                style: {
                                    opacity: 1
                                }
                            },
                            popup: {
                                enable: true,
                                Html: props => {
                                    return `<span>${props.NAME_CHN}:</span><br/>
                             &nbsp;&nbsp;&nbsp;<span style="color: #fe7e07;">确诊人数：${props.totalConfirmed}</span><br/>
                             &nbsp;&nbsp;&nbsp;<span style = "color: #d31b29;">死亡人数：${props.totalDeath}</span><br/>
                             &nbsp;&nbsp;&nbsp;<span style="color: #3f8516;">治愈人数：${props.totalCured}</span>`;
                                }
                            }
                        });
                    })

                });

            });
        }



    }

</script>

